___CSS_LOADER_EXPORT___.push([module.id, ".lbl[data-v-2655a3b6]{background:#e9e9e9}.lbl.checked[data-v-2655a3b6]{background-image:linear-gradient(90deg,#e33232,#ff7220);color:#fff}.wrapper[data-v-2655a3b6]{cursor:pointer;display:block;line-height:24px;padding-right:35px;position:relative;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.wrapper input[data-v-2655a3b6]{cursor:pointer;height:0;opacity:0;position:absolute;width:0}.checkmark[data-v-2655a3b6]{border:1.5px solid #fff;border-radius:100%;position:absolute;right:5px;top:5px}.wrapper:hover input~.checkmark[data-v-2655a3b6]{background-color:#e33232}.checkmark[data-v-2655a3b6]:after{content:\"\";display:none;position:absolute}.wrapper input:checked~.checkmark[data-v-2655a3b6]:after{display:block}.wrapper .checkmark[data-v-2655a3b6]:after{border:solid #fff;border-width:0 1.5px 1.5px 0;height:10px;left:5px;top:2px;transform:rotate(41deg);width:6px}", "",{"version":3,"sources":["webpack://./resources/js/Shared/Checkbox2.vue"],"names":[],"mappings":"AAkDA,sBACA,kBACA,CACA,8BACA,uDAAA,CACA,UACA,CACA,0BAIA,cAAA,CAHA,aAAA,CAIA,gBAAA,CAFA,kBAAA,CADA,iBAAA,CAIA,wBAAA,CACA,qBAAA,CACA,oBAAA,CACA,gBACA,CAEA,gCAGA,cAAA,CACA,QAAA,CAFA,SAAA,CADA,iBAAA,CAIA,OACA,CAEA,4BAKA,uBAAA,CADA,kBAAA,CAHA,iBAAA,CAEA,SAAA,CADA,OAIA,CAEA,iDACA,wBACA,CAOA,kCACA,UAAA,CAEA,YAAA,CADA,iBAEA,CAEA,yDACA,aACA,CAEA,2CAMA,iBAAA,CAAA,4BAAA,CAFA,WAAA,CAHA,QAAA,CACA,OAAA,CAKA,uBAAA,CAJA,SAKA","sourcesContent":["<template>\r\n  <label class=\"lbl ml-3 leading-8 h-8 font-light wrapper flex items-center  text-sm leading-6 py-1 px-2 rounded-full\" :class=\"{'checked':isChecked}\">\r\n    {{label}}\r\n    <input class=\"checkbox\" type=\"checkbox\" :checked=\"isChecked\" :value=\"value\" @change=\"updateInput\"/>\r\n    <span class=\"checkmark w-5 h-5\"></span>\r\n  </label>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  model: {\r\n    prop: 'modelValue', \r\n    event: 'change'\r\n  },\r\n  props: {\r\n    \"value\": { type: String },\r\n    \"modelValue\": { default: \"\" },\r\n    \"label\": { type: String, required: true, default: ''},\r\n    \"trueValue\": { default: true },\r\n    \"falseValue\": { default: false }\r\n  },\r\n  computed: {\r\n    isChecked() {\r\n      if (this.modelValue instanceof Array) {\r\n        return this.modelValue.includes(this.value)\r\n      }\r\n      // Note that `true-value` and `false-value` are camelCase in the JS\r\n      return this.modelValue === this.trueValue\r\n    }\r\n  },\r\n  methods: {\r\n    updateInput(event) {\r\n      let isChecked = event.target.checked\r\n      if (this.modelValue instanceof Array) {\r\n        let newValue = [...this.modelValue]\r\n        if (isChecked) {\r\n          newValue.push(this.value)\r\n        } else {\r\n          newValue.splice(newValue.indexOf(this.value), 1)\r\n        }\r\n        this.$emit('change', newValue)\r\n      } else {\r\n        this.$emit('change', isChecked ? this.trueValue : this.falseValue)\r\n      }\r\n    }\r\n  }\r\n}\r\n</script>\r\n\r\n<style lang=\"css\" scoped>\r\n.lbl {\r\n  background: #e9e9e9;\r\n}\r\n.lbl.checked {\r\n  background-image: linear-gradient(to right, #e33232, #ff7220);\r\n  color: #fff;\r\n}\r\n.wrapper {\r\n  display: block;\r\n  position: relative;\r\n  padding-right: 35px;\r\n  cursor: pointer;\r\n  line-height: 24px;\r\n  -webkit-user-select: none;\r\n  -moz-user-select: none;\r\n  -ms-user-select: none;\r\n  user-select: none;\r\n}\r\n/* Hide the browser's default checkbox */\r\n.wrapper input {\r\n  position: absolute;\r\n  opacity: 0;\r\n  cursor: pointer;\r\n  height: 0;\r\n  width: 0;\r\n}\r\n/* Create a custom checkbox */\r\n.checkmark {\r\n  position: absolute;\r\n  top: 5px;\r\n  right: 5px;\r\n  border-radius: 100%;\r\n  border: 1.5px solid #fff;\r\n}\r\n/* On mouse-over, add a grey background color */\r\n.wrapper:hover input ~ .checkmark {\r\n  background-color: #e33232;\r\n}\r\n/* When the checkbox is checked, add a blue background */\r\n.wrapper input:checked ~ .checkmark {\r\n  /* background-color:#33cc33;\r\n  border: 1.5px solid #33cc33; */\r\n}\r\n/* Create the checkmark/indicator (hidden when not checked) */\r\n.checkmark:after {\r\n  content: \"\";\r\n  position: absolute;\r\n  display: none;\r\n}\r\n/* Show the checkmark when checked */\r\n.wrapper input:checked ~ .checkmark:after {\r\n  display: block;\r\n}\r\n/* Style the checkmark/indicator */\r\n.wrapper .checkmark:after {\r\n    left: 5px;\r\n    top: 2px;\r\n    width: 6px;\r\n    height: 10px;\r\n    border: solid white;\r\n    border-width: 0 1.5px 1.5px 0;\r\n    transform: rotate(41deg);\r\n}\r\n</style>\r\n\r\n"],"sourceRoot":""}]);
